import some from 'lodash/some'
import forEach from 'lodash/forEach'
import filter from 'lodash/filter'
import map from 'lodash/map'
/**
 * @param {*} name name
 * @param {*} access 
 * @param {*} routes 
 * @description , 
 */
export const isPermission = (name, access, routes) => {
  const routePermissionJudge = (list) => {
    return some(list, item => {
      if (item.children && item.children.length) {
        return routePermissionJudge(item.children)
      } else if (item.name === name) {
        return hasAccess(access, item)
      }
    })
  }
  return routePermissionJudge(routes)
}

/**
 * @description metaaccess
 * @param access 
 * @param route  
 * @returns {Boolean} 
 */
export const hasAccess = (access, route) => {
  if (route.meta && route.meta.access) {
    return hasOneOf(access, route.meta.access)
  } else {
    return true
  }
}
/**
 * @param {Array} target 
 * @param {Array} arr 
 * @description 
 */
export const hasOneOf = (target, arr) => {
  return some(target, _ => arr.indexOf(_) > -1)
}

/**
 * @description menu
 * @param routes
 * @param access
 * @returns {Array}
 */
export const getMenuByRouter = (routes, access) => {
  console.log(access)
  const res = []
  forEach(routes, item => {
    if (!item.meta || (item.meta && !item.meta.isMenuHidden)) {
      const obj = {
        name: item.name,
        meta: item.meta,
        icon: item.meta && item.meta.icon
      }
      if ((hasChild(item) || (item.meta && item.meta.always)) && showThisMenuElement(item, access)) {
        obj.children = getMenuByRouter(item.children, access)
      }
      if (item.meta && item.meta.href) obj.href = item.meta.href
      if (showThisMenuElement(item, access)) res.push(obj)
    }
  })
  return res
}

/**
 * @description 
 * @param item
 * @returns {*|boolean}
 */
export const hasChild = (item) => {
  return item.children && item.children.length > 0
}

/**
 * @description 
 * @param item
 * @param access
 * @returns {boolean}
 */
const showThisMenuElement = (item, access) => {
  if (item.meta && item.meta.access && item.meta.access.length) {
    if (hasOneOf(item.meta.access, access)) {
      return true
    } else {
      return false
    }
  } else {
    return true
  }
}
/**
 * @description 
 * @param routes
 * @returns {Array}
 */
export const getMenuByRouterFilterHiddenMenu = (routes) => {
  const res = []
  forEach(routes, item => {
    if (!item.meta || (item.meta && !item.meta.isMenuHidden)) {
      const obj = {
        name: item.name,
        meta: item.meta,
        icon: item.meta && item.meta.icon
      }
      if (hasChild(item) || (item.meta && item.meta.always)) {
        obj.children = getMenuByRouterFilterHiddenMenu(item.children)
      }
      if (item.meta && item.meta.href) obj.href = item.meta.href
      res.push(obj)
    }
  })
  return res
}

/**
 * @param {Array} arr1
 * @param {Array} arr2
 * @description , 
 */
export const getUnion = (arr1, arr2) => {
  return Array.from(new Set([...arr1, ...arr2]))
}

/**
 * @param matchedRoutes
 * @param homeRoute
 * @returns {*[]}
 */
export const getBreadCrumbList = (matchedRoutes, homeRoute) => {
  const filterHideRoutes = filter(matchedRoutes, item => {
    return item.meta && !item.meta.hide
  })
  const formatRoutes = map(filterHideRoutes, item => {
    const obj = {
      icon: (item.meta && item.meta.icon) || '',
      name: item.name,
      meta: item.meta
    }
    return obj
  }).filter(item => item.name !== '_home').filter(item => item.name !== 'home')
  const filterHiddenMenu = filter(formatRoutes, item => !item.meta.isMenuHidden)
  return [Object.assign(homeRoute, { to: homeRoute.path }), ...filterHiddenMenu]
}
/**
 * @description name/params/query
 * @param {*} route1 
 * @param {*} route2 
 */
export const routeEqual = (route1, route2) => {
  const params1 = route1.params || {}
  const params2 = route2.params || {}
  const query1 = route1.query || {}
  const query2 = route2.query || {}
  return (route1.name === route2.name) && objEqual(params1, params2) && objEqual(query1, query2)
}

/**
 * @param {*} obj1 
 * @param {*} obj2 
 * @description 
 */
export const objEqual = (obj1, obj2) => {
  const keysArr1 = Object.keys(obj1)
  const keysArr2 = Object.keys(obj2)
  if (keysArr1.length !== keysArr2.length) return false
  else if (keysArr1.length === 0 && keysArr2.length === 0) return true
  /* eslint-disable-next-line */
  else return !some(keysArr1, key => obj1[key] != obj2[key])
}

/**
 * @param {Array} list 
 * @param {String} name name
 */
export const getNextRoute = (list, route) => {
  let res = {}
  if (list.length === 2) {
    res = getHomeRoute(list)
  } else {
    const index = list.findIndex(item => routeEqual(item, route))
    if (index === list.length - 1) res = list[list.length - 2]
    else res = list[index + 1]
  }
  return res
}

/**
 * @description 
 * @param routers
 */
export const getHomeRoute = routers => {
  let i = -1
  let len = routers.length
  let homeRoute = {}
  while (++i < len) {
    let item = routers[i]
    if (item.children && item.children.length) {
      let res = getHomeRoute(item.children)
      if (res.name) return res
    } else {
      if (item.name === 'home') homeRoute = item
    }
  }
  return homeRoute
}

/**
 * 
 */
export const routeHasExist = (tagNavList, routeItem) => {
  let len = tagNavList.length
  let res = false
  doCustomTimes(len, (index) => {
    if (routeEqual(tagNavList[index], routeItem)) res = true
  })
  return res
}

/**
 * @param {Number} times 
 * @param {Function} callback 
 */
export const doCustomTimes = (times, callback) => {
  let i = -1
  while (++i < times) {
    callback(i)
  }
}

/**
 * @param {*} list 
 * @param {*} newRoute 
 * @description newRoute
 */
export const getNewTagList = (list, newRoute) => {
  const { name, path, meta, query, params } = newRoute
  let newList = [...list]
  if (newList.findIndex(item => item.name === name) >= 0) {
    return newList
  } else {
    newList.push({ name, path, meta, query, params })
  }
  return newList
}
